import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/About.vue')
  },
  {
    path: '/projects',
    name: 'Projects',
    component: () => import('../views/Projects.vue')
  },
  {
    path: '/skills',
    name: 'Skills',
    component: () => import('../views/Skills.vue')
  },
  {
    path: '/project/recipes-vue',
    name: 'Recipes Vue',
    component: () => import('../views/Recipes-vue.vue')
  },
  {
    path: '/project/node-blog',
    name: 'Node blog',
    component: () => import('../views/Blog-node.vue')
  },
  {
    path: '/project/vue-blog',
    name: 'Vue blog',
    component: () => import('../views/Blog-vue.vue')
  },
  {
    path: '/project/node-gym',
    name: 'Gym node',
    component: () => import('../views/Gym-node.vue')
  },
  {
    path: '/project/vue-anime-search',
    name: 'Anime vue',
    component: () => import('../views/Anime-vue.vue')
  },
  {
    path: '/project/vue-landing-page',
    name: 'Landing vue',
    component: () => import('../views/Landing-vue.vue')
  },
  {
    path: '/project/html-landing-page',
    name: 'Landing HTML',
    component: () => import('../views/Landing-html.vue')
  },
  {
    path: '/project/react-inventory',
    name: 'React inventory',
    component: () => import('../views/Inventory-app.vue')
  }
]

const router = new VueRouter({
  scrollBehavior() {
    return {x: 0, y: 0}
  },
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
