<template>
  <nav class="navbar">
    <h1>
      <router-link class="logo-link" to="/">My portfolio</router-link>
    </h1>
    <ul class="nav-list" :class="{active: isActive}">
      <li>
        <router-link class="nav-link" to="/projects">My projects</router-link>
      </li>
      <li>
        <router-link class="nav-link" to="/about">About me</router-link>
      </li>
      <li>
        <router-link class="nav-link" to="/skills">My skills</router-link>
      </li>
    </ul>
    <i @click="toggleBar" :class="{'fas fa-bars': !isActive, 'fas fa-times': isActive}"></i>
    <!-- <i @click="toggleBar" v-if="isActive" class="fas fa-times"></i> -->
  </nav>
</template>

<script>
export default {
  name: 'Navbar',
  data() {
    return {
      isActive: false
    }
  },
  methods: {
    toggleBar() {
      this.isActive = !this.isActive;
    }
  },
  watch: {
    $route() {
      this.isActive = false;
    }
  }
};
</script>

<style lang="scss">
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $primary-color;
  height: 80px;
  box-shadow: 2px 4px 8px 2px rgba($color: #000000, $alpha: 0.5);;
  z-index: 1;

  h1 {
    margin-left: 15px;

    .logo-link {
      color: $bg-color;
      text-decoration: none;
    }
  }

  .nav-list {
    display: flex;

    li {
      margin: 0 10px;
      list-style-type: none;

      .nav-link {
        text-decoration: none;
        color: $bg-color;
        padding: 10px 15px;
        border-radius: 12px;
        transition: 0.3s ease all;

        &:hover {
          background-color: $secondary-color;
        }
      }
    }
  }

  i {
    display: none;
  }
}

@media (max-width: 600px) {
  .navbar {
    position: sticky;
    top: 0;

    .nav-list {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      position: absolute;
      margin-top: 0;
      top: 80px;
      height: calc(100vh - 80px);
      z-index: 3;
      width: 50%;
      background-color: $primary-color;
      transform: translateX(-100%);
      transition: 0.5s ease all;

      li {
        .nav-link {
          font-size: 1.5rem;
        }
      }
    }

    .nav-list.active {
      transform: translateX(0);
    }

    i {
      display: block;
      color: $bg-color;
      font-size: 1.5rem;
      margin-right: 15px;
    }
  }
}
</style>
